@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Farro&display=swap);
* {margin: 0; padding: 0; box-sizing: border-box;}
body {font-size: 16px; font-family: 'Noto Sans KR', sans-serif;}
a {color: inherit; text-decoration: none}
ul, ol {list-style: none }
.row {max-width: 1200px; margin: 0 auto; padding: 0 20px}
.cf:after {content: ''; display: block; clear: both}
img {vertical-align: middle }
/* width:100%;  */
button {cursor: pointer; border: none}
fieldset {display: none}
legend {display: none}
h1, h2, h3, h4, h5, h6 {font-size: 100%}
table {width: 100%; border-collapse: collapse; table-layout: fixed}
.blind {position: absolute; width: 1px; height: 1px; overflow: hidden; text-indent: -9999px;}

.common-table {
    width: 80%;
    margin: 0 auto;
    border-spacing: 0;
  }
  
  .common-table-header-column {
    border-bottom: 3px double #009222;
    background: #74a881;
    border-top: 3px solid #009222;
    padding: 0;
    font-size: 20px;
    padding: 10px 5px;
    font-weight: bold;
    text-align: center;
  }
  .common-table-header-column:nth-child(2) {text-align: left; padding-left: 50px;}
  .common-table-row:hover {background-color: #eceaea; cursor: pointer; }
  .common-table-row {border-bottom: 1px solid #eceaea}
  .common-table-column {padding: 10px 5px;  }
  
  .common-table col:nth-child(1) {width: 7%;}
  .common-table-column:nth-child(1) {color: #ffc200; text-align: center;}
  .common-table col:nth-child(2) {width: auto;}
  .common-table-column:nth-child(2) {color: #009222; padding-left: 50px;}
  .common-table col:nth-child(3) {width: 17%;}
  .common-table-column:nth-child(3) {color: #999; text-align: center;}
